.navBar {
    grid-area: navBar;
    background-color: #efe5d0;
    font-family:      Georgia, "Times New Roman", Times, serif;
    margin-left: 10px;
    margin-bottom: 10px;
    justify-content: center;
    font-size: medium;
}
.navFriends {
    margin-left: 10px;
    margin-bottom: 10px;
    text-align: center;
    font-size: large;
    color:            #675c47;

}

.nav {
    link: black;
    text-align: left;

}
a:link {
    color:            #b76666;
    text-decoration:  none;
    border-bottom:    thin dotted #b76666;
}
a:visited {
    color:            #675c47;
    text-decoration:  none;
    border-bottom:    thin dotted #675c47;
}
a.active{
    color:indianred;
}
ul {
    list-style-type: none;
}
