.wrapper {
    margin: 0px auto;
    background-color: #b5a789;
    width: 1400px;
    display: grid;
    grid-template-areas:
            "heading heading"
            "navBar cont";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 150px 1fr;
    grid-gap: 10px;
    grid-auto-rows: minmax(90px, auto);

}

.wrapper-content {
    grid-area: cont;
    background-color: #efe5d0;
    margin-right: 10px;
    margin-bottom: 10px;
}
